
.scene {
  --ink: #0f1115;
  --paper: #f6f0e6;
  --accent: #d86b3b;
  --accent-strong: #b64523;
  --deep: #1b212a;
  --soft: #f3dcc4;
  --glow: rgba(216, 107, 59, 0.35);
  --teal: #2e6f6d;
  --mist: rgba(255, 255, 255, 0.6);
  font-family: "PingFang SC", "Songti SC", "STSong", "SimSun", serif;
  color: var(--ink);
  display: flex;
  justify-content: center;
  padding: 32px 16px 40px;
  position: relative;
  background: linear-gradient(150deg, #fef8f2 0%, #f5e7d8 45%, #e8d2c0 100%);
  border-radius: 24px;
  overflow: hidden;
}

.ambient-layer {
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 10% 20%, rgba(216, 107, 59, 0.2), transparent 45%),
    radial-gradient(circle at 90% 10%, rgba(46, 111, 109, 0.18), transparent 40%),
    radial-gradient(circle at 80% 80%, rgba(15, 17, 21, 0.08), transparent 45%);
  pointer-events: none;
}

.phone {
  width: min(380px, 100%);
  position: relative;
  z-index: 1;
}

.phone-shell {
  background: linear-gradient(160deg, #161a22, #0c0f14);
  padding: 14px;
  border-radius: 38px;
  box-shadow: 0 25px 60px rgba(15, 17, 21, 0.4);
  position: relative;
}

.phone-bezel {
  background: #0b0f14;
  border-radius: 30px;
  padding: 10px;
  position: relative;
}

.screen {
  background: linear-gradient(180deg, #fef9f4 0%, #f6ede3 100%);
  border-radius: 24px;
  padding: 18px 16px 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  aspect-ratio: 9 / 19.5;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.08);
}

.notch {
  position: absolute;
  top: 6px;
  left: 50%;
  transform: translateX(-50%);
  width: 130px;
  height: 26px;
  background: #0b0f14;
  border-radius: 0 0 16px 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  z-index: 3;
}

.notch .camera {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #1b212a;
  box-shadow: 0 0 6px rgba(95, 168, 255, 0.6);
}

.notch .speaker {
  width: 40px;
  height: 4px;
  border-radius: 4px;
  background: #1c222c;
}

.topbar {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  color: #5b4a3a;
  margin-top: 18px;
}

.status-icons {
  display: flex;
  align-items: center;
  gap: 8px;
}

.signal,
.wifi {
  width: 18px;
  height: 10px;
  background: linear-gradient(90deg, #2e6f6d, #7ab7b0);
  border-radius: 4px;
  mask: linear-gradient(90deg, transparent 0 20%, #000 20% 40%, transparent 40% 60%, #000 60% 80%, transparent 80%);
}

.battery {
  font-weight: 600;
}

.hero {
  display: grid;
  grid-template-columns: 70px 1fr auto;
  gap: 12px;
  align-items: center;
  position: relative;
}

.avatar-ring {
  position: relative;
  width: 68px;
  height: 68px;
}

.avatar {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background: linear-gradient(135deg, #2e6f6d, #d86b3b);
  box-shadow: 0 0 0 3px #f5e4d2, 0 10px 18px rgba(0, 0, 0, 0.2);
}

.pulse {
  position: absolute;
  inset: -6px;
  border-radius: 24px;
  border: 1px solid rgba(216, 107, 59, 0.4);
  animation: pulse 2.5s ease-in-out infinite;
}

.role-meta h1 {
  font-family: "ZCOOL XiaoWei", serif;
  font-size: 26px;
  letter-spacing: 2px;
  margin: 0;
}

.role-title {
  font-size: 11px;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: #9b6e55;
  margin: 0 0 4px;
}

.role-subtitle {
  font-size: 11px;
  color: #6f5d4d;
  margin: 4px 0 0;
}

.status-pill {
  background: var(--deep);
  color: #fef9f4;
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 11px;
  box-shadow: 0 6px 10px rgba(15, 17, 21, 0.2);
}

.status-panel,
.chat-panel {
  background: #ffffff;
  border-radius: 20px;
  padding: 14px;
  box-shadow: 0 10px 24px rgba(15, 17, 21, 0.1);
  border: 1px solid rgba(216, 107, 59, 0.15);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.panel-header h2 {
  font-size: 14px;
  margin: 0;
}

.chip {
  background: var(--soft);
  color: #7a3c2a;
  font-size: 10px;
  padding: 4px 8px;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
  margin-bottom: 12px;
}

.stat-card {
  background: var(--paper);
  border-radius: 12px;
  padding: 8px;
  display: grid;
  gap: 4px;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.stat-card .label {
  font-size: 10px;
  color: #815f4a;
}

.stat-card .value {
  font-size: 13px;
  font-weight: 700;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.stat-card .note {
  font-size: 10px;
  color: #a0674c;
}

.bars {
  display: grid;
  gap: 8px;
}

.bar-row {
  display: grid;
  grid-template-columns: 48px 1fr 36px;
  align-items: center;
  gap: 8px;
  font-size: 11px;
  color: #5d4a3b;
}

.bar {
  height: 8px;
  background: #f1e4d6;
  border-radius: 999px;
  position: relative;
  overflow: hidden;
}

.bar i {
  display: block;
  height: 100%;
  border-radius: 999px;
  background: linear-gradient(90deg, var(--accent), var(--accent-strong));
}

.chat {
  display: grid;
  gap: 8px;
}

.bubble {
  padding: 8px 10px;
  border-radius: 14px;
  font-size: 12px;
  line-height: 1.4;
  position: relative;
}

.bubble.incoming {
  background: #f1e4d6;
  color: #3a2a20;
  align-self: flex-start;
}

.bubble.outgoing {
  background: #1b212a;
  color: #f8f4ef;
  align-self: flex-end;
}

.stamp {
  font-size: 9px;
  color: rgba(0, 0, 0, 0.4);
  display: block;
  margin-top: 4px;
}

.bubble.outgoing .stamp {
  color: rgba(255, 255, 255, 0.5);
}

.input-bar {
  margin-top: 10px;
  background: #f6ede3;
  border-radius: 12px;
  padding: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  color: #8d6c56;
}

.send-btn {
  border: none;
  background: var(--accent);
  color: #fff;
  padding: 6px 12px;
  border-radius: 10px;
  font-size: 11px;
  letter-spacing: 1px;
}

.side-buttons {
  position: absolute;
  right: -6px;
  top: 80px;
  display: grid;
  gap: 8px;
}

.side-buttons span {
  width: 4px;
  height: 30px;
  background: #1b212a;
  border-radius: 8px;
}

.side-buttons .long {
  height: 50px;
}

.device-label {
  text-align: center;
  color: #5d4a3b;
  font-size: 12px;
  letter-spacing: 4px;
  margin-top: 16px;
}

.stagger {
  animation: rise 0.8s ease both;
  animation-delay: calc(var(--delay, 1) * 0.1s);
}

@keyframes rise {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.05);
  }
}

@media (max-width: 520px) {
  .screen {
    padding: 16px 14px 18px;
  }

  .hero {
    grid-template-columns: 60px 1fr;
    grid-template-rows: auto auto;
  }

  .status-pill {
    grid-column: 2 / 3;
    justify-self: start;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }
}
